    import { GpnButton, GpnTabs, GpnTextField, type TabItem } from "gpn-ui";
    import { useGlobalStore } from "../stores/store";
    import { ref } from "vue";

    const store = useGlobalStore();

    const tabs = ref<TabItem[]>([
