        {
            id: 3,
            name: "Оценка компетенций",
            to: "/ca",
        },
    ]);
    const tabsClosable = ref<TabItem[]>([]);
    for (let i = 0; i < 10; i++) {
        tabsClosable.value.push({
            id: i,
            name: `Вкладка ${i + 1}`,
            closable: true,
            icon: "bi-person",
        });
    }
    const selTab = ref(2);
    const selClosableTab = ref(0);

    function addTab() {
        const maxId = Math.max(...tabsClosable.value.map((m) => parseInt(m.id + "")));
        tabsClosable.value.push({
            id: maxId + 1,
            name: `Новая вкладка ${maxId + 1}`,
            closable: true,
        });
    }
</script>

